<template>
    <div id="new-student">
        <h3>Add New Student</h3>
        <div class="row">
            <form @submit.prevent="saveStudent" class="col s12">
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Student ID Number</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="student_id" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Student Name</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Address</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="address" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Birthday</p>
                    <div class="input-field col s12">
                        <input type="date" v-model="birthday" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Birthplace</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="birthplace" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Grade Level</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="grade_level" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Gender</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="gender" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Father's Name</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="father_name" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Father's Number</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="father_no" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Father's Occupation</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="father_occupation" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Mother's Name</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="mother_name" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Mother's Number</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="mother_no" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Mother's Occupation</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="mother_occupation" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">LRN Number</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="lrn_no" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">Religion</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="religion" required>
                    </div>
                </div>
                <div class="row">
                    <p class="col s12 edit-field" style="margin: 0px;">School Year</p>
                    <div class="input-field col s12">
                        <input type="text" v-model="school_year" required>
                    </div>
                </div>
                <div class="view-options">
                    <button type="submit" class="btn btn-large bg-icon">Submit</button>
                    <router-link to="/s" class="btn btn-large grey">Cancel</router-link>
                </div>
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'new-student',
    data () {
        return {
            student_id: null,
            name: null,
            gender: null,
            birthday: null,
            address: null,
            grade_level: null,
            birthplace: null,
            father_name: null,
            father_no: null,
            father_occupation: null,
            mother_name: null,
            mother_no: null,
            mother_occupation: null,
            lrn_no: null,
            religion: null,
            school_year: null
        }
    },
    methods: {
        saveStudent() {
            db.collection('students').add({
                student_id: this.student_id,
                name: this.name,
                gender: this.gender,
                address: this.address,
                grade_level: this.grade_level,
                birthday: this.birthday,
                birthplace: this.birthplace,
                father_name: this.father_name,
                father_no: this.father_no,
                father_occupation: this.father_occupation,
                mother_name: this.mother_name,
                mother_no: this.mother_no,
                mother_occupation: this.mother_occupation,
                lrn_no: this.lrn_no,
                religion: this.religion,
                school_year: this.school_year
            }).then(docRef => {
                this.$router.push('/s')
            })
            //in case anything goes wrong
            .catch(error = console.log(err))
        }
    }
}
</script>


